import React, { useState, useEffect } from 'react';
import './Category.css';
import { getProductByCategory } from '../../Redux/Actions/productActions';
import { useDispatch } from 'react-redux';
import { Link } from 'react-router-dom';

const Category = () => {

    const dispatch =useDispatch();

    const [ dropDown, setDropDown ] = useState(false)

    const handleClick = () =>{
        setDropDown(!dropDown)
    }

    const fetchMenClothing = () => {
        dispatch(getProductByCategory("men's clothing"))
        setDropDown(!dropDown)
    }
    const fetchWomenClothing = () => {
        dispatch(getProductByCategory("women's clothing"))
        setDropDown(!dropDown)
    }
    const fetchJewellery = () => {
        dispatch(getProductByCategory("jewelery"))
        setDropDown(!dropDown)
    }
    const fetchElectronics = () => {
        dispatch(getProductByCategory("electronics"))
        setDropDown(!dropDown)
    }

    return(
        <>
        <div className="category-wrapper">
                <div className="category" onClick={ () => handleClick()} >Category ^</div>
                {dropDown ? <div className="category-dropdown">
                    <Link to="/products" className="link">
                        <div className="ct-btn mensclothing" onClick={ () => fetchMenClothing()} value="menclothing">Men's Clothings</div>
                    </Link>
                    <Link to="/products" className="link">
                        <div className="ct-btn womensclothing" onClick={ () => fetchWomenClothing()}>Women's Clothings</div>
                    </Link>
                    <Link to="/products" className="link">
                        <div className="ct-btn jewellery" onClick={ () => fetchJewellery()}>Jewellery</div>
                    </Link>
                    <Link to="/products" className="link">
                        <div className="ct-btn electronics" onClick={ () => fetchElectronics()}>Electronics</div>
                    </Link>
                </div> : <></>}
        </div>
        </>
    )
}
export default Category;