import React, { useEffect } from 'react';
import './Detail.css'
import Navbar from '../../Components/Navbar/Navbar';
import Category from '../../Components/CategoryDropDown/Category';
import Footer from '../../Components/Footer/Footer';
import { useParams } from 'react-router';
import { useSelector,useDispatch } from 'react-redux';
import { getSingleProduct } from '../../Redux/Actions/productActions';
import { addToCart } from '../../Redux/Actions/cartActions';
import { hideModel } from '../../Redux/Actions/showModel';


const Detail = () => {
    const {id} = useParams();
    const dispatch = useDispatch();
    const products = useSelector((state) => state.productsByCategory.categoryProducts)
    const loading = useSelector((state) => state.singleProduct.loading)
    const showModel = useSelector((state) => state.showModel.showModel)
    const selectedProduct = useSelector((state) => state.singleProduct.selectedProduct)


    useEffect(() => {
        dispatch(getSingleProduct(id))
    },[])

    //For Heading
    let heading = ''
    if( selectedProduct ){
        heading = selectedProduct.category
    }

    const addItem = () => {
        console.log('add')
        dispatch(addToCart(selectedProduct,selectedProduct.id,selectedProduct.price,1))
    }

    const closeModel = () => {
        dispatch(hideModel())
    }

    return(
        <>
        <div className="detail-page-container">
            <Navbar />
        {loading ? <h1>Loading</h1> :
        <>
                    <div className="productPage-title-wrapper">
                        <div className="productPage-title">
                                {heading}
                            </div>
                        </div>
                    <div className="bby-header">
                        <div className="pp-category-wrapper">
                            <Category />
                        </div>
                    </div>
        </>}
            <div className="product-detail-container">
                <div className="product-detail-img-wrapper">
                    <img src={selectedProduct.image} className="product-detail-img"></img>
                </div>
                <div className="product-detail">
                    <div className="product-title">{selectedProduct.title}</div>
                    <div className="product-description">{selectedProduct.description}</div>
                    <div className="product-rating"><span className="r">Rating</span></div>
                    <div className="product-price">{selectedProduct.price} $</div>
                    <div className="product-cta">
                        <div className="add-to-cart-btn" onClick={() => addItem()}>
                            Add to Cart
                        </div>
                        <div className="wish-list">
                            WishList
                        </div>
                    </div>

                    
                </div>

            </div>
            <div className="detail-footer">
                    <Footer />
            </div>
                {showModel ? <>
                    <div className="model-wrapper">
                        <div className="model">
                            <div className="model-title">
                                Live up your lucid dream<br/>with lucid products.
                            </div>
                            <div className="model-subtitle">
                                We appreciate your support.
                            </div>
                            <div className="close-model" onClick={() => closeModel()}>
                                Back
                            </div>
                        </div>
                    </div>

                </> : ''}
        </div>
        </>
    )
}
export default Detail;